a {
  text-decoration: none;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 0;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-amount {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin: 0 auto 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.stars__block {
  justify-content: flex-start;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
}

.stars__block-star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: block;
}

.stars__block-star:last-child {
  margin-right: 0;
}

.stars__block--4 :nth-child(-n+4) {
  background: url("star-active.edda4c43.svg") center no-repeat;
}

.stars__reviews p {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

